import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        productIds: [],
        option: {
            bx_param: localStorage.getItem('bx_param') || 0.4,
            bj_param: localStorage.getItem('bj_param') || 0.2
        }
    },
    mutations: {
        setIds(state, code) {
            state.productIds.push(code);
            state.productIds = Array.from(new Set(state.productIds));
        },
        setOptionBx(state, param) {
            localStorage.setItem('bx_param', param);
            state.option.bx_param = param;
        },
        setOptionBj(state, param) {
            localStorage.setItem('bj_param', param);
            state.option.bj_param = param;
        }
    },
    actions: {
        /**
         * Example usage in Vue components: store.dispatch('setAuthToken', token);
         * @param {Store} context Signature: context.state, context.commit, etc..
         * @param {Object} code
         */
        setProductDishes(context, code) {
            context.commit('setIds', code);
        },
    }
});

export default store;
