<template>
  <div id="app">
    <Header/>
    <div class="container pt-fixed pb-2">
      <router-view />
    </div>
    <b-alert
        v-model="showBasket"
        class="position-fixed fixed-bottom m-0 rounded-0 basket-dialog"
        style="z-index: 2000;"
        variant="success"
        :class="[{'h-100': isOpen}]"
        dismissible
    >
      <div class="basket" v-if="basket.length">
        <b-button v-if="!isOpen" @click="isOpen = true" class="btn-open" size="sm" variant="success">показать</b-button>
        <b-button v-else @click="isOpen = false" class="btn-close" size="sm" variant="success">скрыть</b-button>
        <div v-for="(item, index) in basket" :key="index">
          {{item.doc.title}}
        </div>
      </div>
    </b-alert>
  </div>
</template>
<script>
import Header from '@/components/Header.vue'
// import Footer from "./components/Footer.vue";

export default{
  name: 'App',
  components: {
    Header
  },
  data() {
    return {
      isOpen: false,
      showBasket: false,
      basket: []
    }
  },
  computed: {
    productIds() {
      return this.$store.state.productIds;
    }
  },
  watch: {
    productIds: function (val) {
      if(val.length) {
        this.showBasket = true;
        this.setDishesBasket();
      }
    }
  },
  methods: {
    async setDishesBasket() {
      try {
        await this.$pouch.allDocs({
          // selector: { is_home: 1 },
          // fields: ['_id', 'title'],
          keys: this.productIds
        }).then((res) => {
          this.basket = res.rows
        });
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>
<style lang="scss">
html, body, #app {
  height: 100%;
  min-height: 100%;
}
.pt-fixed {
  padding-top: 90px!important;
}
#app {}
.basket-dialog.h-100 {
  padding-top: 90px;
}
.basket {
  position: relative;
  .btn-open, .btn-close {
    position: absolute;
    top: 40px;
    right: -63px;
  }
}
</style>
