import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../pages/Home.vue";

Vue.use(VueRouter)


const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/category",
    name: "Category",
    component: async () => import('../pages/Category/Category.vue'),
  },
  {
    path: "/dishes",
    name: "Dishes",
    component: async () => import('../pages/Dishes/Dishes.vue'),
  },
  {
    path: '/category/:id',
    name: 'CategoryPage',
    component: async () => import('../pages/Category/CategoryPage.vue'),
  },
  {
    path: '/category/:id/:page',
    name: 'Product',
    component: async () => import('../pages/Category/Product.vue'),
  },
  {
    path: '/edit/:id',
    name: 'Edit',
    component: async () => import('../pages/Edit.vue'),
  },
  {
    path: "/new",
    name: "New",
    component: async () => import('../pages/New.vue'),
  },
  {
    path: "/setting",
    name: "Setting",
    component: async () => import('../pages/Setting.vue'),
  },
  {
    path: "/calculator",
    name: "Calculator",
    component: async () => import('../pages/Calculator.vue'),
  },
]

const router = new VueRouter({
  mode: 'history',
  base:  process.env.NODE_ENV === 'production'
      ? '/sugar/'
      : '/',
  routes
})

export default router
