<template>
  <div class="home">
    <h3>За все продукты</h3>
    <div class="calculate-product mb-3">
        <div class="product-information w-100 mb-2 d-flex text-center">
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">БЖ</span>
            <span class="information__footer">{{ calcFixed(bx_var) }}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">У</span>
            <span class="information__footer">{{ calcFixed(y_var) }}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">ХЕ</span>
            <span class="information__footer">{{ calcFixed(xe_var) }}</span>
          </div>
        </div>
    </div>
    <div class="calculate-hr w-100 mb-3"></div>
    <h3 class="d-flex align-items-center">
      Продукты
      <a href="#" @click.prevent="removeAll" class="btn btn-danger ml-auto">Убрать все</a>
    </h3>
    <div class="calculate-items d-flex flex-wrap" v-if="products.length">
      <div class="calculate-item" v-for="(item, index) in products" :key="index">
        <div class="card">
          <router-link :to="`/category/${item.category_id}/${item._id}`" class="bd-placeholder-img card-img-top w-100 d-flex align-items-center justify-content-center">
            <img class="mx-auto img-fluid" v-if="item.image" :src="item.image"/>
          </router-link>
          <div class="card-body">
            <p class="card-text">{{item.title}}</p>
            <div class="product-information w-100 mb-2 d-flex text-center">
              <div class="information flex-fill d-flex flex-column">
                <span class="information__header">БЖ</span>
                <span class="information__footer">{{ bxbjCalc(bxCalc(item.product_b, item.gram), bjCalc(item.product_j, item.gram))}}</span>
              </div>
              <div class="information flex-fill d-flex flex-column">
                <span class="information__header">У</span>
                <span class="information__footer">{{ byCalc(item.product_y, item.gram)}}</span>
              </div>
              <div class="information flex-fill d-flex flex-column">
                <span class="information__header">ХЕ</span>
                <span class="information__footer">{{ allCalc(bxCalc(item.product_b, item.gram),bjCalc(item.product_j, item.gram),byCalc(item.product_y, item.gram))}}</span>
              </div>
            </div>
            <div class="d-flex align-items-center">
              <span>За {{item.gram}} гр.</span>
              <b-button variant="danger" class="ml-auto" @click.prevent="setProducts(item, index)">Убрать</b-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      products: [],
      bx_var: 0,
      y_var: 0,
      xe_var: 0,
    }
  },
  computed: {
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    calculate(value, gram) {
      return (parseFloat(value) * gram) / 100
    },
    bxCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value, gram) {
      return (this.calculate(value, gram) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    calcFixed(v) {
      return v.toFixed(1);
    },
    plusAllCalculate(items) {
      items.forEach((item)=> {
        if (item.is_home === 1) {
          this.bx_var = this.bx_var + (parseFloat(this.bxCalc(item.product_b, item.gram)) + parseFloat(this.bjCalc(item.product_j, item.gram)));
          this.y_var = this.y_var + (parseFloat(this.byCalc(item.product_y, item.gram)));
          this.xe_var = this.xe_var + (parseFloat(this.bxCalc(item.product_b, item.gram)) + parseFloat(this.bjCalc(item.product_j, item.gram)) + parseFloat(this.byCalc(item.product_y, item.gram)))
        }
      })
    },
    async getProducts() {
      try {
        await this.$pouch.find({
          selector: { is_home: 1 },
        }).then((res) => {
          this.products = res.docs;
          this.plusAllCalculate(this.products);
        });
      } catch (error) {
        console.error(error)
      }
    },
    async setProducts(item, index) {
      try {
        item.is_home = 0;
        await this.$pouch.put(item);
        this.products.splice(index, 1);
        this.bx_var =  0;
        this.y_var = 0;
        this.xe_var = 0;
        this.plusAllCalculate(this.products);
      } catch (error) {
        console.error(error)
      }
    },
    async removeAll() {
      try {
        this.products.forEach(value => {
          value.is_home = 0
        })
        await this.$pouch.bulkDocs(this.products)
        this.products = [];
        this.bx_var =  0;
        this.y_var = 0;
        this.xe_var = 0;
        this.plusAllCalculate(this.products);
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.getProducts();
  }
}
</script>

<style scoped lang="scss">
.home {
  .calculate-hr {
    background-color: #d1e7dd;
    height: 5px;
  }
  .product-information {
    .information {
      margin: 0 2px;
      .information__header {
        border-bottom: 1px solid green;
      }
      .information__footer {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
  .calculate-product {}
  .calculate-items {
    margin: 0 -5px;
    .calculate-item {
      width: 50%;
      margin-bottom: 5px;
    }
    .card {
      margin: 0px 5px 5px;
      .card-img-top {
        background-color: #ecf5f1;
        height: 100px;
      }
      .card-body {
        padding: 5px;
      }
      .card-text {
        min-height: 50px;
      }
    }
    .product-information {
      .information {
        .information__footer {
          font-size: 16px;
        }
      }
    }
  }
}
</style>
